<template>
  <div style="width:100%; height:100%; display:block; background:#fff;">
    <div>
      <MobileHeader
        title="아이디 찾기"
        right-icon="mdi-help-circle-outline"
        @close="routingBack"
      />

      <div id="m_search-id">
        <section v-if="!certify">
          <StepDesc desc1="이름과 휴대폰 번호를" desc2="입력해 주세요" />

          <section>
            <div id="certify-number">
              <div>
                <v-text-field
                  v-model="member.memberName"
                  type="text"
                  color="main"
                  placeholder="이름을 입력해주세요"
                />
              </div>
              <div class="phone-box">
                <v-text-field
                  v-model="member.memberPhone"
                  type="text"
                  color="main"
                  placeholder="휴대폰 번호"
                  maxlength="11"
                  @input="changePhone"
                  @keyup.enter="certifyTrans"
                >
                  <template #append>
                    <button @click="certifyTrans">
                      {{ certifyTransFlag ? "다시요청" : "인증요청" }}
                    </button>
                  </template></v-text-field
                >
              </div>
              <div v-if="certifyTransFlag" class="address">
                <!-- <div class="address"> -->
                <v-text-field
                  v-model="certifyNumber"
                  type="text"
                  placeholder="인증번호 6자리"
                  maxlength="6"
                  color="main"
                >
                  <template slot="append">
                    <p style="font-size: 14px" class="mb-0">{{ TimerStr }}</p>
                  </template>
                </v-text-field>
              </div>
            </div>
          </section>

          <div
            id="next-btn__box"
            :class="{ active: certifyNumber && certifyNumber.length === 6 }"
          >
            <button @click="certifyCertify">
              다음
            </button>
          </div>
        </section>
        <section v-else>
          <StepDesc desc1="아이디 찾기가" desc2="완료되었습니다" />
          <div class="search-id__box">
            <span
              v-for="(SeachIdMember, index) in getMemberIdSearchList"
              :key="index"
              >{{ "아이디 : " + SeachIdMember.id }}
            </span>
          </div>
          <div>
            <button class="default-button main mb-4" @click="move('/login')">
              로그인
            </button>
            <button class="default-button disable" @click="move('/searchPw')">
              비밀번호 찾기
            </button>
          </div>
        </section>
      </div>
    </div>

